body{
    background: black;
    width: 100vw;
}
.naming{
    text-align: center;
    margin: 0;
    color: darkblue;
}
.slogan{
    color:chartreuse;
    position: absolute;
    top: 25vh;
    font-size: 5em;
}
#independentHeader{
    font-size: 5vw;
}
.intro_matrix{
    height: calc(100vh - 36px);
    width: 100vw;
    mask-image: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}
.about{
    font-size: 5em;
    font-weight: 900;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: darkgoldenrod;
    text-align: center;
    height: 100vh;
}
.cards{
    width: 70%;
    height:25vh;
    margin-top: 100px;
    margin-left: 15%;
    background:coral;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
}
.cards:hover{
    background: black;
    color: coral;
}
.cards:hover p a{
    color: coral;
}
.cards p{
    text-align: center;
    vertical-align: middle;
    font-size: 5em;
}
.cards p a{
    text-decoration: none;
    color: black;
    font-size: 4vw;
}
.startingAnchor{
    text-align: center;
    margin-top: 10vh;
}
.startingAnchor a{
    text-decoration: none;
    color: white;
    background: blue;
    font-size: xx-large;
    font-family: fantasy;
    vertical-align: middle;
    padding: 25px;
    border-radius: 25px;
}
.startingAnchor:hover a{
    background: black;
    color: chartreuse;
}
.functionalityH1{
    text-align: left;
    color: chocolate;
}
.functionalityNav{
    position: absolute;
    display: inline-flex;
    top: 0;
    right: 0;
}
.functionalityCards{
    width: 70%;
    height:10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    margin-right: 15px;
}
.functionalityCards:hover{
    background: black;
    color: coral;
}
.functionalityCards:hover a{
    color: white;
}
.functionalityCards p{
    text-align: center;
    vertical-align: middle;
}
.functionalityCards p a{
    text-decoration: none;
    color: coral;
}
.orderQuery, .boldedQueryLevel{
    color: darkgreen;
    font-size: larger;
}
.matrix_notation{
    color: crimson;
}
input{
    width: 66px;
    height: 66px;
    font-size: 22px;
}
.resultSpan{
    color: crimson;
    font-size: xx-large;
    background: cyan;
    margin: 5px;
}
input{
    text-align: center;
}

.menuIcon{
    display: none;
}
.resulta{
    color:red;
    font-size: 4vw;
    font-weight: 900;
}
@media(max-width:770px){
    #independentHeader{
        font-size: 10vw;
    }
    .functionalityNav{
        position: relative;
        left: 0;
        top: 50px;
        border-top: 6px solid coral;
        border-bottom: 6px solid coral;
    }
    .menuIcon{
        position: absolute;
        display: inline;
        top: 0;
        right: 36px;
    }   
    .cart{
        margin-top: 100vh;
    }
    .cards p a{
        font-size: 10vw;
    }
    .resulta{
        font-size: 9vw;
    }
}
